$dev: #ffffff;

@font-face {
  font-family: 'proxima-nova';
  src: url('/assets/fonts/ProximaNova-Regular.otf');
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: 'proxima-nova';
  color: #2a2e30;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto';
  font-weight: 500;
}

p {
  font-size: 20px;
  line-height: 29px;
}

nav {
  background-color: $dev;
  padding: 25px;
  text-align: center;
  border-bottom: 1px solid #d0d7de;

  .logo {
    position: absolute;
    font-family: 'Roboto';
    font-size: 22px;
    font-weight: 600;
    margin-top: -1px;
  }
  
  li{
    list-style: none;
    display: inline-block;
  }
  a {
    color: #24292f;
    font-size: 18px;
    font-weight: 400;
    padding: 0 15px;
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
  }
  .return {
    position: absolute;
    right: 25px;
    font-family: 'Roboto'!important;
    font-size: 14px!important;
    margin-top: 3px;
    background-color: #ffff004f;
  }
}


@media only screen and (max-width: 1200px) {
  .sidebar {
    position: relative!important;
    width: 100%!important;
    ul ul {
      padding-left: 18px!important;
    }
    }
    
    #toc {
      font-size: 18px;
      margin: 0px!important;
      padding-right: 0px;
      margin-right: 0;
      border-right: none!important;
    }
}

.jekyll-twitter-plugin {
  margin: 0 auto;
  text-align: center;
}


@media only screen and (max-width: 680px) {
  .logo {
    position: relative!important;
    padding-bottom: 7px;
  }
  .return {
    display: none;
  }

  .section-nav {
    padding-left: 20px;
  }
  .blog-item {
    padding: 0;
    li {
      list-style-type: none;
    }
  }
  .content img {
    width: 100%!important;
  }
}



body {
  margin: 0;
}

.content {
  padding: 20px;
  background: $dev;
  max-width: 700px;
  margin: 10px auto 20px auto;

  a {
    color: #2a7ae2;
    text-decoration: none;
  }
  a:visited {
    color: #1756a9;
  }
  table {
    margin: 0 auto;
  }
  img {
    max-width: 600px;
  }
  li {
    font-size: 20px; 
    line-height: 29px;
    padding-bottom: 10px;
  }
}

#comments {
  margin-top: 130px;
}

.username {
  font-family: monospace;
}

.comment-button {
  background: #bbf0ff;
  border: none;
  color: #000!important;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
  float: right;
  font-family: monospace;
  margin-top: -25px;
  
}

.date-posted {
  font-size: 10px;
  display: inline;
  margin: -10px 0 -20px;
  font-family: monospace;
}

.author-badge {
  font-size: 10px;
  background: #bbf0ff;
  border-radius: 5px;
  padding: 3px;
  font-family: monospace;
  top: -2px;
  position: relative;
}

.comment-edited {
  font-size: 10px;
  color: #ff3950;
  border-radius: 5px;
  padding: 3px;
  font-family: monospace;
  display: block;
  margin: -8px 0 -8px -3px;
}

.comments-list {
  list-style-type: none;
  padding-left: 0;
}

.comment-body p{
  margin-top: 0px;
  font-size: 17px;
  font-family: monospace;
}

.sidebar {
  position: fixed;
  width: 0px;
  display: block;
  ul ul {
    padding-left: 10px;
  }
  
  }
  
  
  .sidebar div {
  padding: 8px;
  font-size: 24px;
  display: block;
  }

.fade-in {
  opacity: 1;
  transition: opacity 1s ease-in-out;
}

.fade-out {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

  
#date-author {
  font-size: 15px;
  margin-top: -15px;
}
hr {
    margin: 30px auto 20px auto;
    height: 1px;
    width: 100px;
    border: none;
    background: -webkit-gradient(linear, 0 0, 100% 0, from(transparent), to(transparent), color-stop(50%, black));
}

footer {
  font-size: 14px;
  text-align: center;
  margin-bottom: 30px;
}

